// File: src/assets/styles/page/modal.scss
.modal-overlay {
  .modal-content {
    position: relative;
    background: #ffffff;
    padding: 20px;
    border-radius: 8px;
    max-width: 500px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    animation: fadeIn 0.3s ease;

    .modal-title {
      font-size: 1.8rem;
      font-weight: 600;
      color: #000000;
      margin-bottom: 10px;
    }

    .modal-message {
      font-size: 1.4rem;
      color: #6b7280;
      margin-bottom: 20px;
      word-break: break-word;
      overflow-wrap: break-word;
      white-space: normal;
    }

    .modal-buttons {
      display: flex;
      justify-content: flex-end;
      gap: 10px;
      .modal-btn {
        padding: 8px 16px; // Padding nh hn admin__form-button mt cht
        border: none;
        border-radius: 4px; // Bo gc ging admin__action-btn
        font-size: 1.4rem; // ng b vi admin__form-button
        font-weight: 500; // Font-weight nh hn mt cht
        cursor: pointer;
        transition:
          background-color 0.3s ease,
          transform 0.2s ease; // Hiu ng ging admin__add-button

        &--cancel {
          background: #ccc; // Mu xm nht
          color: #fff;
          &:hover {
            background: darken(#ccc, 10%);
            transform: translateY(-2px); // Hiu ng nhc ln ging admin__add-button
          }
          &:active {
            transform: translateY(0); // Tr li khi nhn
          }
        }

        &--confirm {
          background: #ff4d4d; // Mu  ging admin__logout v admin__action-btn--delete
          color: #fff;
          &:hover {
            background: darken(#ff4d4d, 10%);
            transform: translateY(-2px);
          }
          &:active {
            transform: translateY(0);
          }
        }
      }
    }
  }
}

// Hiu ng fade in cho modal
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

// Responsive
@media (max-width: 768px) {
  .modal-content {
    width: 90%;
    padding: 15px; // Gim padding trn mobile
  }

  .modal-title {
    font-size: 1.6rem; // Gim kch thc ch
  }

  .modal-message {
    font-size: 1.3rem;
  }

  .modal-btn {
    padding: 6px 12px; // Gim kch thc nt
    font-size: 1.3rem;
  }
}
